<template>
	<div class="cy_page1">
		<div class="cont box-border-shadow">
			<div style="width: 60%;margin: 0 auto;">
				<el-row>
				  <el-col :span="20">
				  	<el-row>
					  <el-col :span="3" style="text-align: center;margin-top: 8px;font-size: 18px">
					  	<span>支持度:</span>
					  </el-col>
					  <el-col :span="21">
					  	<el-slider v-model="value" :min="Number(min_max.zhichi[0])*100" :max="Number(min_max.zhichi[1])*100" :format-tooltip="formatTooltip" :step="(Number(min_max.zhichi[1])*100-Number(min_max.zhichi[0])*100)/10"></el-slider>
					  </el-col>
					</el-row>
					<el-row>
					  <el-col :span="3" style="text-align: center;margin-top: 8px;font-size: 18px">
					  	<span>置信度:</span>
					  </el-col>
					  <el-col :span="21">
					  	<el-slider v-model="value2" :min="Number(min_max.zhixin[0])*100" :max="Number(min_max.zhixin[1])*100" :format-tooltip="formatTooltip" :step="(Number(min_max.zhixin[1])*100-Number(min_max.zhixin[0])*100)/10"></el-slider>
					  </el-col>
					</el-row>
					<el-row>
					  <el-col :span="3" style="text-align: center;margin-top: 8px;font-size: 18px">
					  	<span>提升度:</span>
					  </el-col>
					  <el-col :span="21">
					  	<el-slider v-model="value3" :min="Number(min_max.tisheng[0])*100" :max="Number(min_max.tisheng[1])*100" :format-tooltip="formatTooltip" :step="(Number(min_max.tisheng[1])*100-Number(min_max.tisheng[0])*100)/10"></el-slider>
					  </el-col>
					</el-row>
					<el-row>
					  <el-col :span="3" style="text-align: center;margin-top: 8px;font-size: 18px">
					  	<span>杠杆率:</span>
					  </el-col>
					  <el-col :span="21">
					  	<el-slider v-model="value4" :min="Number(min_max.ganggan[0])*100" :max="Number(min_max.ganggan[1])*100" :format-tooltip="formatTooltip" :step="(Number(min_max.ganggan[1])*100-Number(min_max.ganggan[0])*100)/10"></el-slider>
					  </el-col>
					</el-row>
					<el-row>
					  <el-col :span="3" style="text-align: center;margin-top: 8px;font-size: 18px">
					  	<span>确信度:</span>
					  </el-col>
					  <el-col :span="21">
					  	<el-slider v-model="value5" :min="Number(min_max.quexin[0])*100" :max="Number(min_max.quexin[1])*100" :format-tooltip="formatTooltip" :step="(Number(min_max.quexin[1])*100-Number(min_max.quexin[0])*100)/10"></el-slider>
					  </el-col>
					</el-row>
				  </el-col>
				  <el-col :span="4" style="text-align: center;margin-top: 75px;">
				  	<el-button plain @click="getData()">提交</el-button>
				  </el-col>
				</el-row>
			</div>
			<div>
				<div v-show="showGraph" style="position: relative;">
					<div id="graphCharts" :style="{width:windowWidth+'px'}" style="height: 800px;margin: 0 auto"></div>
				</div>
				<div v-show="!showGraph" style="width: 100%;height: 800px;">
					<img style="display:block;margin: 100px auto;" src="../../../assets/no-sj.png">
				</div>
			</div>
		</div>
	</div>
</template>
<script type="text/javascript">
	import * as echarts from 'echarts'
	import reqData from "./../../../reqData.js"
	import amazeui from 'amazeui'
	import './../image/jquery.easyui.min.js'
	import Cropper from 'cropperjs'
	import 'cropper/dist/cropper.css'
	export default{
		name:'cw_yw',
		data(){
			return {
				value: 0,
				value2: 0,
				value3: 0,
				value4: 0,
				value5: 0,
				min_max:{},
				showGraph:false,
				windowWidth:(window.innerWidth)*0.85,
			}
		},
		components:{
		},
		computed:{
			company(){
				return JSON.parse(sessionStorage.getItem("company"));
			},
		},
		methods:{
		  formatTooltip(val) {
	        return val / 100;
	      },
		  unique(arr){
			var newArr = [arr[0].name];
	   		var data=[arr[0]];
	   		//去重复项
	  　　 	for(var i=1;i<arr.length;i++){
	　　　　		if(newArr.indexOf(arr[i].name) == -1){
	          　　  newArr.push(arr[i].name);
	          	    data.push(arr[i]);
	    　　  	}
    		}
    		//区分值和主题
    		console.log(data.length)
    		for(var i=0;i<data.length;i++){
    		 	if(data[i].tag&&data[i].tag==='业务'){
    		 		data[i].itemStyle={
			             normal: {
			                 color: '#68bdf6',
			                 borderColor:'#5ca8db',
			                 borderWidth:2,
			             }
			         }
			         data[i].symbol='diamond'
    		 	}
    		}
    		console.log(data);
	        return data;
		  },
		  getData(){
		  	 this.showGraph=true;
		 	 var graphCharts = echarts.init(document.getElementById('graphCharts'));
		 	 graphCharts.showLoading();
		 	 reqData
		        .req({
		          apiName: "shenji_caiwu_yewu_zhishitupu",
		           data: {   
		             biaoshi:'4',          
		             zhichi:this.value/100,
		             zhixin:this.value2/100,
		             tisheng:this.value3/100,
		             ganggan:this.value4/100,
		             quexin:this.value5/100,
		           }
		        })
		        .then(res => {
		          res = res.data;
		          if (res && res.code === "200") {
		            let data = res.data;
		            console.log(data);
		            let nodes=data.nodes
					let links=data.links;
					nodes=this.unique(nodes);
					graphCharts.hideLoading();
					if(nodes.length>0){
						this.showGraph=true;
						graphCharts.setOption(this.graphOption(nodes,links)); 
			    	    $(window).resize(function(){
							graphCharts.resize({
								width:(window.innerWidth)*0.85,
								height:'auto'
							})
					    })
					}else{
						this.showGraph=false
					}
			    	
		          }
		        })
		        .catch(err => {
		          console.log(err);
		          this.showGraph=false
		       });
		  },
		  graphOption(nodes,links){
		    	let option = {
				     title: {
				         
				     },
				     tooltip: {
				         formatter: function(x) {
				         	let result;
				         	if(x.data.name){
				             	result=x.data.name;
				         	}else{
								let name=x.data.value;	
								name=name.replace(/;/g,";<br/>")
				         		result=x.data.source+" → "+x.data.target+"<br/>"+name;
				         	};
				         	return result;
				         }
				     },
				     legend: {
				        x: "center",
				        show: true,
				        data: ["财务指标", "业务指标"]
				     },
				     series: [{
				        type: 'graph',
				        layout: 'force',
			            categories: [{
			                name: '财务指标',
			                itemStyle:{
			                	color: '#ff756e',
					            borderColor:'#e06760',
								borderWidth: 2,
								
			                },
							symbol: "diamond"
			            }, {
			                name: '业务指标',
			                itemStyle: {
			                    normal: {
					               borderColor: "#5ca8db",
					               color: "#68bdf6",
					               borderWidth:2,
					            },
			                }
			            }],
				         focusNodeAdjacency: true,
				         symbolSize: 68,
				         roam: true,
				         edgeSymbol: ['', 'arrow'],
				         // edgeSymbolSize: [80, 10],
				         hoverAnimation:true,
				         force: {
				         	initLayout:'circular',
				             repulsion: 1620,
            				 // layoutAnimation:false,
            				 gravity:0.5,
				             edgeLength: [86, 180],
				             layoutAnimation:true
				         },
				         draggable: true,
						 // symbol:'diamond',
				         itemStyle: {
				             normal: {
				                 color: '#ff756e',
				                 borderColor:'#e06760',
				                 borderWidth:2,
				             },

				         },
				         lineStyle: {
				             normal: {
				                 width: 1,
				                 color: '#000'

				             }
				         },
				         edgeLabel: {
				             normal: {
				                 show: true,
				                 textStyle: {fontSize: 14,color:'#000'},
				                 formatter: function(x) {
				                 	let name=x.data.value;
						         	if(name.length>5){
										name=name.substr(0,5)+'...';
										// v.nameSub=name;
									}else{
										name=name.substr(0,5);
										// v.nameSub=name;
									}
					                return name;
					            }
				             }
				         },
				         label: {
				         	show:true,
				            formatter: function(x) {
			                 	let name=x.data.name;
					         	if(name.length>5){
									name=name.substr(0,5)+'...';
									// v.nameSub=name;
								}else{
									name=name.substr(0);
									// v.nameSub=name;
								}
								
			                     return '{a|'+name+'}';
			                },
			                rich: {
						        a: {
						            color: '#fff',
						        },
						    }
				         },
				        nodes: nodes, 
						links:links 
				    }]
				};
				return option;
		  },
		  getKedu(){
		  	reqData
		        .req({
		          apiName: "shenji_caiwu_yewu_zhishitupu_kedu",
		           data: {    
		           	 biaoshi:'4'
		          }
		        })
		        .then(res => {
		          res = res.data;
		          if (res && res.code === "200") {
		            let data = res.data;
		            console.log(data);
			    	this.min_max=data;
			    	this.showGraph=true;
			    	this.getData();
		          }
		        })
		        .catch(err => {
		          console.log(err);
		       });
		    }
		},
	    mounted(){
	    	this.getKedu();
	    	// this.getData();
	    }
	}
</script>
<style scoped="">
	.cont{
		width: 100%;
		padding:40px;
		box-sizing: border-box;
	} 
</style>



